@import url(https://fonts.googleapis.com/css?family=Nunito:400,700);
html {
  line-height: 1.6;
  font-family: 'Nunito', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #222222;
}

h1,
h2,
h3,
h4,
h5 {
  line-height: 1.2;
  color: #333333;
}

.container {
  display: flex;
  justify-content: space-around;

}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1{
  margin: 15px 0;
  text-align: center
}

.container{
  display: flex; 
}

.aside-view {
  flex: 0 0 55%;
}

.map-view {
  flex: 0 0 40%;
  display: flex;
  flex-direction: column;
}

.con-cards{
  flex: 0 0 90%;
  padding: 30px;
  display: flex;
  justify-content: stretch;
}

.cards{
  display: flex;
  justify-content: space-around;
}
.constituencies-card {
    box-shadow: 2px 6px 25px rgba(0, 0, 0, 0.1);
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    position: relative;
    background-color: #befba6;
}

.constituencies-card:hover {
    box-shadow: 2px 6px 25px rgba(0, 0, 0, 0.2);
}

.con-links {
    padding: 15px 30px 30px 30px;
    display: flex;
    flex-wrap: wrap;
}

.title {
    margin: 0;
    padding: 15px 0 0 0;
}

.constituency {
    margin: 0 15px 0 0;
    text-decoration: none;
    cursor: pointer;
    padding: 0 5px;
}

.selected {
    background-color: red;
    color: rgb(255, 255, 255);
    font-weight: 700;
}

.display-card {
    box-shadow: 2px 6px 25px rgba(0, 0, 0, 0.1);
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    position: relative;
    justify-content: space-around;
}

.display-card:hover {
    box-shadow: 2px 6px 30px rgba(0, 0, 0, 0.2);
}

.card-content {
    padding: 30px;
}

.winner {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.winner img {
    width: 80px;
    height: 48px;
    margin: 0 0 0 30px;
}

.cadidate {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.cadidate img {
    width: 40px;
    height: 24px;
    margin: 0 0 15px 30px;
}

.subtitle {
    text-align: center;
    font-weight: 700;
    margin: 15px 0;
    color: #333333;
}

.display-card h1 {
    margin: 0;
}

.tdp {
    background-color: #ffd000;
}
.ycp {
    background-color: #006effe1;
}
.js {
    background-color: #ff0202e3;
}

.map {
  padding: 15px;
}

.selected-svg {
  fill: red;
}

.svg-class:hover{
  fill:gold;
}
